import{
    SET_USERINFO,
    SHOWHEADER,
    HIDEHEADER,
    SHOWLOADING,
    HIDELOADING,
    SETRECEDATA,
    CLEAEREDATA,
} from './mutations-type'

export default {
    setUserInfo: ({commit}, res) => {
        commit('SET_USERINFO', res)
    },
    hideLoading: ({commit}) => {
        commit('HIDELOADING')
    },
    showLoading: ({commit}) => {
        commit('SHOWLOADING')
    },
    showHeader: ({commit}) => {
        commit('SHOWHEADER')
    },
    hideHeader: ({commit}) => {
        commit('HIDEHEADER')
    },
    setReceData: ({commit}, res) => {
        commit('SETRECEDATA', res)
    },
    clearReceivableData: ({commit}) => {
        commit('CLEAEREDATA')
    },
}



